<template>
  <div class="about">
    <mainmenu></mainmenu>
    <section class="section" data-type="horizontal">
      <div class="section__cards">
        <div class="section__card main">
          <h1 class="section__title">Section 3a</h1>
        </div>
        <div class="section__card clip1">
          <h1 class="section__title">Section 3b</h1>
        </div>
      </div>
    </section>
    <section class="section">
      <div class="section__card">
        <h1 class="section__title">Section 11</h1>
      </div>
    </section>
    <section class="section">
      <div class="section__card">
        <h1 class="section__title">Section 2</h1>
      </div>
    </section>
   
    <section class="section">
      <div class="section__card">
        <h1 class="section__title">Section 4</h1>
      </div>
    </section>
  </div>
</template>


<script>

import mainmenu from "../components/mainmenu";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
export default {
  mounted() {
    // let create = this.$refs.create;
    gsap.utils.toArray(".section").forEach(section => {
      // Check if section has horizontal scrolling
      if (section.dataset.type === "horizontal") {
        const cards = section.querySelector(".section__cards");
        const card = section.querySelector(".section__card");

        gsap.to(cards, {
          x: () => {
            return -cards.scrollWidth + card.offsetWidth;
          },
          ease: "none",
          scrollTrigger: {
            trigger: section,
            start: () => "center center",
            end: () => `+=${cards.scrollWidth - card.offsetWidth}`,
            scrub: true,
            pin: true,
            invalidateOnRefresh: true,
            anticipatePin: 1
          }
        });

        // Use standard vertical scroll pinning
      } else {
        ScrollTrigger.create({
          markers: true,
          trigger: section,
          start: () => "top top",
          pin: true,
          anticipatePin: 1
        });
      }
    });
  },
  components: {
    mainmenu
  }
};
</script>

